
.book-container {
    width: 90vw;
    margin: 1rem;
    padding: 1rem;
    // background-color: rgba(70, 68, 68, 0.5);
  
    h2 {
      margin-top: -0.3rem;
      color: rgba(189, 189, 189, 0.5);
    }
  
    .book-list {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      
      .book {
        width: 22rem;
        // max-height: 30rem;
        // height: 32rem;
        display: flex;
        flex-direction: column;
        margin: 0.8rem 2.5rem;
        padding-bottom: 1.3rem;
        box-shadow: -1px 4px 9px 2px rgba(0,0,0,0.75);
        background-color: rgba(190, 188, 188, 0.5);
        line-height: 1;
  
        .img-div {
          width: 12rem;
          // height: 10rem;
          margin: 0 auto;
          box-shadow: -1px 4px 9px -1px rgba(0,0,0,0.75);
          // object-fit: cover;
          
          img {
            width: 100%;
            // height: auto;
          }
        }
  
        .btn-read-book {
          width: 8rem;
          margin: 0 auto;
          padding: 0.3rem 0.8rem;
          color: #000;
          border: 1px solid #000;
          border-radius: 0.3rem;
          box-shadow: -1px 4px 9px -1px rgba(0,0,0,0.75);
          text-decoration: none;

          &:hover {
            font-weight: bold;
            box-shadow: -1px 14px 9px -1px rgba(0,0,0,0.6);
          }
  
          a {
            text-decoration: none;
            color: #000;
          }
        }
      }
    }
}

.add-book {
  display: flex;
  flex-direction: column;
  text-align: left;

  input {
    margin-bottom: 0.5rem;
  }


}